.no-logged-main-page h3 {
    text-transform: uppercase;
    color: #69210a;
}

.no-logged-main-page p {
    text-transform: capitalize;
    color: #282828;
    font-weight: bold;
}
.no-logged-main-page ul {
    list-style-type: circle;
}
.no-logged-main-page button {
    border: none;
    outline: none;
    height: 24px;
    width: 60px;
    border-radius: 4px;
    cursor: pointer;
    box-shadow: 0px 10px 24px 0px rgba(105,33,10,1);
    background: #69210a;
    color: white;
    transition: all 0.5s ease;
    margin-top: 15px;
}
.no-logged-main-page button:hover{
    background: #311005;
    box-shadow: 0px 18px 24px 0px rgb(58, 17, 5);
}